
@mixin style-link($color) {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    color: $color; }

// для того, чтобы после применения float дочерний элемент был виден для родителя
@mixin clearfix {
        &::after {
            content: "";
            display: table;
            clear: both; } }

