.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba($color-brown-darck, 0.1) 0.03%, rgba($color-brown-darck, 0.6) 99.94%), url(../../img/header-background.jpg) center / cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 75%);

    &__logo {
        position: absolute;
        left: 7.2rem;
        top: 3.7rem;
        height: 7rem;
        width: 5.6rem; }

    &__box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }
